html {
  background-color: #343a40;
}

body {
  margin: 0;
  padding: 0;
  /* @import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap'); */
  font-family: sans-serif;
}

/* width */
body::-webkit-scrollbar {
  width: 2px;
}

/* Track */
body::-webkit-scrollbar-track {
  background: #343a40;
}

/* Handle */
body::-webkit-scrollbar-thumb {
  background: #00b4d8;
}

/* Handle on hover */
body::-webkit-scrollbar-thumb:hover {
  background: #90e0ef;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0.5;
}

nav {
  transition: all 0.3s cubic-bezier(0.25, 0.45, 0.45, 0.95);
}

nav .container-fluid .nav-profiles .iconn {
  color: #00b4d8;
}

nav .container-fluid .nav-profiles .iconn:hover {
  color: #90e0ef;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

nav .container-fluid .nav-item:hover {
  color: #00b4d8;
  opacity: 1;

  border-bottom: 0.05rem solid #00b4d8;
}

nav .navbar-toggler:hover {
  color: black;
  opacity: 0.7;

  border: 1px solid #00b4d8;
}

@media (max-width: 993px) {
  nav .container-fluid .nav-item:hover {
    color: #00b4d8;

    opacity: 1;
    width: 4rem;
    border-bottom: 0.05rem solid #00b4d8;
  }

  nav .container-fluid .links-size {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.welcome_page {
  height: 100vh;
  background-color: #343a40;
}
.jumbotron {
  border-radius: 0;
  margin: 0;
}
.welcome_page > div {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.bbbb {
  display: flex;
  justify-content: center;
  padding: 0 10px;
  align-items: center;
  transition: all 2s cubic-bezier(0.25, 0.45, 0.45, 0.95);
}
button {
  border: 1px solid #90e0ef;
  background-color: #343a40;

  border-radius: 5px;
  color: #90e0ef;
  padding: 10px;
  outline: none;
  z-index: 5;
  transition: all 0.2s ease-in-out;
}
a:hover {
  text-decoration: none;
}
button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.about-container {
  padding: 7rem 0;
  /* overflow: scroll; */
}
.aaaaa {
  display: flex;
  justify-content: space-evenly;
}

.image-box {
  height: 550px;
  /* height: 600px; */
  box-shadow: 10px 10px 5px #5d6369;
}

@media (max-width: 765px) {
  .image-box {
    height: 450px;
    /* height: 400px; */
  }
}
.text-box {
  /* text-shadow: 5px 5px 10px #90e0ef; */
}
@media (max-width: 768px) {
  .about-container {
    padding: 4rem 0;
    overflow: scroll;
  }
  .image-div {
    padding-bottom: 1.8rem;
  }
}

.footer-body {
  display: flex;
  flex-direction: column;
  /* overflow: scroll; */
}
.footer-content {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.icon-footer {
  font-size: 30px;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.icon-footer:hover {
  color: #90e0ef;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

li {
  list-style: none;
}
.section-body {
  display: flex;
  flex-direction: column;
  /* overflow: scroll; */
}
.section-header {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  /* flex-wrap: wrap; */
  /* padding-left: 9rem; */

  overflow: auto;
}
.section-header::-webkit-scrollbar {
  width: 1px;
  height: 0.2rem;
}

/* Track */
.section-header::-webkit-scrollbar-track {
  background: #343a40;
}

/* Handle */
.section-header::-webkit-scrollbar-thumb {
  background: #00b4d8;
}

/* Handle on hover */
.section-header::-webkit-scrollbar-thumb:hover {
  background: #90e0ef;
}

@media (max-width: 523px) {
  .section-header {
    padding-left: 5rem;
  }
}
@media (max-width: 438px) {
  .section-header {
    padding-left: unset;
    overflow: unset;
    flex-wrap: wrap;
  }
}

.education {
  /* box-shadow: 5px 5px 10px #90E0EF; */
  border-left: 0.2rem solid #00b4d8;
  /* padding: 0 10px; */
  /* margin: 0 auto; */
  /* margin-left: 1rem; */
}

.certts {
  display: flex;
  /* justify-content: space-around; */
  align-items: center;
  border-left: 0.2rem solid #00b4d8;
  /* border-top: 0.2rem solid #00B4D8; */
  /* box-shadow: 5px 5px 10px #90E0EF; */
  padding: 10px;
}
.accolade {
  border-left: 0.2rem solid #00b4d8;
  /* border-right: 0.2rem solid #00B4D8; */
  padding: 10px;
}
.degree-section {
  display: flex;
  justify-content: space-between;
}

.section-header-content {
  padding: 0 0.5rem;
}

.section-header-content:hover {
  color: #00b4d8;
}

.activeSection {
  color: #00b4d8;
  padding-bottom: 5px;
  border-right: 0.2rem solid #00b4d8;
  border-left: 0.2rem solid #00b4d8;
  border-top: 0.2rem solid #00b4d8;
}

.project-content {
  display: flex;
  flex-direction: column;
  /* z-index: 5; */
}

.project-contents,
.project-contents-2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* height: */
  /* padding-left: 9rem; */
  /* overflow: scroll; */
}
.project-contents-2 :nth-child(1) {
  order: 1;
}
.project-contents-2 :nth-child(2) {
  order: 3;
}
.project-contents-2 :nth-child(3) {
  order: 2;
}
.project-name {
  align-self: center;
  color: #00b4d8;
  padding-right: 20px;
  width: 10%;
}
.project-link {
  color: #00b4d8;
}

.project-link:hover {
  text-decoration: none;
  color: #90e0ef;
}
.main-project-content {
  padding-top: 25px;
  width: 35%;
}
.main-project-content-2 {
  width: 50%;
  border: 1px solid #90e0ef;
  box-shadow: 2px 5px 10px #90e0ef;
  display: flex;
  /* flex-direction: column; */
  /* overflow-y: scroll; */
}
.main-project-content-2 :nth-child(1) {
  order: 1;
}
.main-project-content-2 :nth-child(2) {
  order: 2;
}
.main-project-content-2 :nth-child(3) {
  order: 3;
}
.main-project-content,
.main-project-content-2 {
  align-self: center;
  justify-self: center;
  /* width: 49%; */
  height: 300px;
}

.project-briefing {
  width: 92%;
  border-right: 1px solid #90e0ef;
  /* margin: 0 10px; */
  padding: 10px;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.project-briefing::-webkit-scrollbar {
  width: 1px;
  height: 0.2rem;
}

/* Track */
.project-briefing::-webkit-scrollbar-track {
  background: #343a40;
}

/* Handle */
.project-briefing::-webkit-scrollbar-thumb {
  background: #00b4d8;
}

/* Handle on hover */
.project-briefing::-webkit-scrollbar-thumb:hover {
  background: #90e0ef;
}

.project-titles {
  margin: 0 auto;
}
.project-github-link {
  margin: auto 0;
}

.file-icon-click {
  font-size: 2rem;
  padding: 10px;
}

.description ul li {
  position: relative;
  padding-left: 25px;
  margin-bottom: 5px;
}

.description ul li::before {
  content: '\25B9';
  position: absolute;
  left: 5px;
  color: #90e0ef;
}
.icon-before-content-2 {
  display: none;
}

/* MEDIA QUERY */
@media (max-width: 770px) {
  .project-contents,
  .project-contents-2 {
    flex-direction: column;
    align-items: center;
  }
  .main-project-content-2 {
    border: unset;
    box-shadow: unset;
  }
  .project-contents-2 :nth-child(1) {
    order: 1;
  }
  .project-contents-2 :nth-child(2) {
    order: 2;
  }
  .project-contents-2 :nth-child(3) {
    order: 3;
  }
  .main-project-content {
    border-top: 1px solid #90e0ef;
    border-left: 1px solid #90e0ef;
    border-right: 2px solid #90e0ef;
  }
  .main-project-content-2 {
    border-bottom: 1px solid #90e0ef;
    border-left: 1px solid #90e0ef;
    border-right: 1px solid #90e0ef;
    flex-direction: column;
  }
  .project-name {
    width: unset;
  }
  .main-project-content,
  .main-project-content-2 {
    width: 90%;
    padding: 0px;
    margin: 0;
    height: 250px;
  }

  .project-titles {
    display: none;
  }
  .project-briefing {
    width: unset;
    height: 92%;
    border-right: unset;
    border-bottom: 1px solid #90e0ef;
  }
  .project-github-link {
    margin: 0 auto;
  }
  .file-icon-click {
    font-size: 1.2rem;
    padding: 10px;
  }
  .icon-before-content::before {
    content: 'Code';
    font-size: 1.2rem;
  }
  .icon-before-content-2 {
    display: unset;
  }
  .icon-before-content-2::after {
    content: 'Live';
    font-size: 1.2rem;
  }
}

